.burger-btn {
  position: relative;

  width: 24px;
  height: 18px;

  background-color: #ffffff;
  border: none;

  &::before,
  &::after {
    content: "";

    position: absolute;

    width: 24px;
    height: 2px;

    background-color: #000000;
  }

  &::before {
    top: 0;
    left: 0;

    box-shadow: 0 8px 0 0 #000000;
  }

  &::after {
    bottom: 0;
    left: 0;
  }

  &--no-js {
    display: none;
  }

  &--cross {
    &::before {
      box-shadow: none;
      top: 50%;
      margin-top: -1px;
      transform: rotate(45deg);
      transition: 0.2s;
    }

    &::after {
      top: 50%;
      margin-top: -1px;
      transform: rotate(-45deg);
      transition: 0.2s;
    }
  }
}
